import { CheckCircle } from "@phosphor-icons/react";
import { useEffect, useState } from "react";
import {
  Button,
  Card,
  Col,
  Container,
  Dropdown,
  Form,
  Row,
} from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import routes from "~/config/router";

export default function Hiring() {
  const [selectedJob, setSelectedJob] = useState(null);
  const [selectedDate, setSelectedDate] = useState(null); // Chn ngy phng vn
  const navigate = useNavigate();
  const [isOk, setIsOk] = useState(false);

  const jobs = [
    {
      title: "WhatsApp social and media management",
      details: "Social and Media",
      type: "Social",
    },
    {
      title: "Product Marketing Manager, WhatsApp Privacy and Compliance",
      details: "Consumer Marketing+4 more ",
      type: "Sales & Marketing",
    },
    {
      title: "Product Marketing Manager, WhatsApp AI",
      details: "Artificial Intelligence +3 more",
      type: "Artificial Intelligence +4 more",
    },
    {
      title: "Brand Marketing Manager, WhatsApp",
      details: "WhatsApp ",
      type: "Marketing",
    },
    {
      title: "QA Engineering Lead - WhatsApp",
      details: "Data & Analytics +2 more",
      type: "Analytics",
    },
  ];

  const handleJobSelect = (index) => {
    setSelectedJob(index);
  };

  const isFormValid = selectedJob !== null && selectedDate !== null;

  const handleSubmit = () => {
    setIsOk(true);
  };

  useEffect(() => {
    const user = JSON.parse(sessionStorage.getItem("user"));
    if (!user) {
      navigate(routes.login);
    }
  }, [navigate]);

  return (
    <div style={{ backgroundColor: "#f7f7f7", minHeight: "100vh" }}>
      <Container>
        {!isOk ? (
          <>
            <Row className="py-4">
              <Col md={8} className="text-start">
                <h1>We're Hiring</h1>
              </Col>
              <Col md={4}>
                <Form className="d-flex">
                  <Form.Control
                    type="search"
                    placeholder="Search WhatsApp Positions"
                    className="me-2"
                  />
                  <Button variant="dark">Search</Button>
                </Form>
              </Col>
            </Row>

            <Row className="w-100">
              <Col md={8}>
                <Row className="mb-3">
                  <Col className="text-start">
                    <h5>Viewing {jobs.length} Jobs Related To: WhatsApp</h5>
                  </Col>
                  <Col className="text-end">
                    <Dropdown>
                      <Dropdown.Toggle variant="light" id="dropdown-basic">
                        Results per page: 1
                      </Dropdown.Toggle>
                      <Dropdown.Menu>
                        <Dropdown.Item href="#/action-1">10</Dropdown.Item>
                      </Dropdown.Menu>
                    </Dropdown>
                  </Col>
                </Row>

                {/* Duyt qua cc job v to Card */}
                {jobs.map((job, index) => (
                  <Col xs={12} key={index} className="mb-3">
                    {" "}
                    {/* t Card trong Col chim 12 ct */}
                    <Card
                      style={{
                        cursor: "pointer",
                        border:
                          selectedJob === index
                            ? "2px solid blue"
                            : "1px solid #ccc",
                      }}
                      onClick={() => handleJobSelect(index)}
                    >
                      <Card.Body className="d-flex justify-content-between">
                        <div>
                          <Card.Title className="fw-normal text-start">
                            {job.title}
                          </Card.Title>
                          <Card.Text className="text-start fs-6 text-body-secondary">
                            {job.details}
                            <br />
                            {job.type}
                          </Card.Text>
                        </div>
                        <CheckCircle
                          size={24}
                          color={selectedJob === index ? "blue" : "gray"}
                        />
                      </Card.Body>
                    </Card>
                  </Col>
                ))}
              </Col>

              <Col md={4}>
                <Row className="mb-3">
                  <Col className="text-start">
                    <h5>Interview Date</h5>
                  </Col>
                  <DatePicker
                    selected={selectedDate}
                    onChange={(date) => setSelectedDate(date)}
                    className="form-control m-3"
                    placeholderText="Select a date"
                    dateFormat="dd/MM/yyyy"
                  />
                </Row>

                <Row className="mb-3">
                  <Button
                    variant="primary"
                    className="m-3"
                    disabled={!isFormValid}
                    onClick={handleSubmit}
                    style={{ width: "100%" }} // m bo nt chim ton b chiu rng
                  >
                    Submit
                  </Button>
                </Row>
              </Col>
            </Row>
          </>
        ) : (
          <>
            <Row
              className="d-flex justify-content-center align-items-center vh-100"
              style={{ padding: "50px 0" }}
            >
              <Col xs={12} md={10} lg={8} className="text-center">
                <h3 style={{ padding: "20px", color: "green" }}>
                  You have successfully registered, we will send you a specific
                  time to schedule an interview via email!
                </h3>
              </Col>
            </Row>
          </>
        )}
      </Container>
    </div>
  );
}
