import { ArrowCircleRight, CaretDown, List } from "@phosphor-icons/react";
import { useState } from "react";
import { Col, Container, Image, Nav, Offcanvas, Row } from "react-bootstrap";
import { Link, useNavigate } from "react-router-dom";
import { img } from "~/assets";
import routes from "~/config/router";
import "./styles.css";

export default function Home() {
  const [showMenu, setShowMenu] = useState(false);
  const navigate = useNavigate();
  const handleClose = () => setShowMenu(false);
  const handleShow = () => setShowMenu(true);

  return (
    <div className="warper">
      <Container>
        <Row>
          <Col xs={6} md={4} className="text-start">
            <Image src={img.logo} />
          </Col>
          <Col xs={12} md={8} className="text-end">
            <div className="menu">
              <Link
                className="link"
                to="login"
                style={{
                  textDecoration: "underline",
                  textDecorationThickness: "3px",
                }}
              >
                Jobs
              </Link>
              <Link className="link" to="login">
                Areas of Work
              </Link>
              <Link className="link" to="login">
                Locations
              </Link>
              <Link className="link" to="login">
                Career Programs
              </Link>
              <Link className="link" to="login">
                How We Work
              </Link>
              <Link className="link" to="login">
                Blog
              </Link>
              <Link className="link" to="login">
                Login <CaretDown size={20} />
              </Link>
            </div>
            {/* Mobile menu icon */}
            <span
              className="d-md-none"
              onClick={handleShow}
              style={{ cursor: "pointer" }}
            >
              <List size={32} />
            </span>
          </Col>
        </Row>

        <Row className="mt-5">
          <Col xs={12} md={5} className="text-start">
            <Row>
              <Col xs={12} className="mb-4">
                <p className="mb-3 text-white">Areas of Work</p>
                <h1 className="mb-3 text-white">WhatsApp</h1>
                <p className="mb-3 text-white">
                  <ArrowCircleRight size={32} /> View 4 Open Positions
                </p>
              </Col>
              <Col xs={12} className="text-center text-md-start">
                <Image src={img.phone} height={558} width={299} />
              </Col>
            </Row>
          </Col>
          <Col xs={12} md={7} className="text-center text-md-end">
            <Image
              width={850}
              height={470}
              src={img.background}
              className="img-fluid"
            />
            <Row className="text-center w-100 pt-5">
              <h1 className="mb-5">Simplifying connections for all</h1>
              <p>
                WhatsApp from Meta is the easy way to connect through messages,
                voice and video calls. Were making it possible to chat with
                anyone, no matter where they are. Join us as we empower the
                world to connect through secure, reliable messaging.
              </p>
            </Row>
          </Col>
        </Row>
      </Container>
      <Image
        onClick={() => navigate(routes.login)}
        style={{ marginTop: "100px" }}
        src={img.all}
        className="img-fluid"
      />

      {/* Offcanvas menu for mobile */}
      <Offcanvas show={showMenu} onHide={handleClose}>
        <Offcanvas.Header closeButton></Offcanvas.Header>
        <Offcanvas.Body>
          <Nav className="flex-column">
            <Link
              className="link"
              style={{ color: "black" }}
              to="login"
              onClick={handleClose}
            >
              Jobs
            </Link>
            <Link
              className="link"
              style={{ color: "black" }}
              to="login"
              onClick={handleClose}
            >
              Areas of Work
            </Link>
            <Link
              className="link"
              style={{ color: "black" }}
              to="login"
              onClick={handleClose}
            >
              Locations
            </Link>
            <Link
              className="link"
              style={{ color: "black" }}
              to="login"
              onClick={handleClose}
            >
              Career Programs
            </Link>
            <Link
              className="link"
              style={{ color: "black" }}
              to="login"
              onClick={handleClose}
            >
              How We Work
            </Link>
            <Link
              className="link"
              style={{ color: "black" }}
              to="login"
              onClick={handleClose}
            >
              Blog
            </Link>
            <Link
              className="link"
              style={{ color: "black" }}
              to="login"
              onClick={handleClose}
            >
              Login <CaretDown size={20} />
            </Link>
          </Nav>
        </Offcanvas.Body>
      </Offcanvas>
    </div>
  );
}
